import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {FacturationRoutes} from './facturation.routing';
import {FacturationService} from "./service/facturation.service";
import {FacturationListComponent} from "./facturation.list/facturation.list.component";
import {FacturationEditComponent} from "./facturation.edit/facturation.edit.component";
import {ClientService} from "../client/service/client.service";
import {BanqueService} from "../admin/banque/service/banque.service";
import {MaintenanceService} from "../maintenance/service/maintenance.service";
import {ReglementService} from "../reglement/service/reglement.service";
import {DataTableModule} from "angular2-datatable";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(FacturationRoutes)
  ],
  declarations: [
    FacturationListComponent,
    FacturationEditComponent,
  ],
  providers: [
    FacturationService,
    ClientService,
    BanqueService,
    MaintenanceService,
    ReglementService
  ],
})

export class FacturationModule {
}



