import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {PageTitleService} from '../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../core/route-animation/route.animation";
import {Facturation} from "../metier/facturation.metier";
import {FacturationService} from "../service/facturation.service";
import {BaseComponent} from "../../core/component/base-component";
import {Params, ActivatedRoute} from "@angular/router";
import {Client} from "../../client/metier/client.metier";
import {ClientService} from "../../client/service/client.service";
import {CustomValidators} from "ng2-validation/dist/index";
import {BanqueService} from "../../admin/banque/service/banque.service";
import {Banque} from "../../admin/banque/metier/banque.metier";
import {MaintenanceService} from "../../maintenance/service/maintenance.service";
import {Maintenance} from "../../maintenance/metier/maintenance.metier";
import {Reglement} from "../../reglement/metier/reglement.metier";
import {ReglementService} from "../../reglement/service/reglement.service";

@Component({
  selector: 'ms-facturation-edit',
  templateUrl: './facturation.edit.component.html',
  styleUrls: ['./facturation.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class FacturationEditComponent extends BaseComponent implements OnInit {

  private facturation: Facturation;
  public facturationForm: FormGroup;
  private reglement: Reglement = new Reglement;
  private etatPaiementList: string[] = [];
  private typePaiementList: string[] = [];
  private clientList: Array<Client> = [];
  private banqueList: Array<Banque> = [];


  constructor(private fb: FormBuilder,
              private route: ActivatedRoute,
              private pageTitleService: PageTitleService,
              private facturationService: FacturationService,
              private reglementService: ReglementService,
              private clientService: ClientService,
              private banqueService: BanqueService) {
    super();
  }

  ngOnInit() {
    this.loadFacturation();
    this.pageTitleService.setTitle("PAYER UNE FACTURATION");
    this.facturationForm = this.fb.group({
      id: '',
      numero: '',
      date: [null, Validators.compose([Validators.required, CustomValidators.date])],
      client: [null, Validators.required],
      etatPaiement: ['', Validators.required],
      typePaiement: ['', Validators.required],
      montantReste: [0, Validators.required],
      montant: [0, Validators.required],
      banque: null,
      numeroCheque: '',
    });
  }

  loadFacturation() {
    this.route.params.switchMap((params: Params) => this.facturationService.getFacturation(+params['id']))
      .subscribe(facturation => {
          this.facturation = facturation;
          this.facturationForm.patchValue(this.facturation);
          this.facturationForm.get("date").setValue(new Date().toISOString().substring(0, 10));
          if (this.facturation.etatPaiement == 'PAID') {
            this.disableFacturation();
          }
          this.loadEtatPaiementList();
          this.loadTypePaiementList();
          this.loadClientList();
          this.loadBanqueList();
        }, error => this.showError(error.status, error.json().message)
      );
  }

  createReglement(facturationForm: any) {

    this.reglement.montant = facturationForm.montantReste;
    this.reglement.date = facturationForm.date;
    this.reglement.typePaiement = facturationForm.typePaiement;
    this.reglement.facturation = this.facturation;
    this.reglement.banque = facturationForm.banque;
    this.reglement.numeroCheque = facturationForm.numeroCheque;

    if (this.reglement.montant <= 0) {
      this.router.navigate(['/facturation/list']); // Redierction vers list

      return;
    }

    this.reglementService.saveReglement(this.reglement)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/facturation/list']); // Redierction vers list

      }, error => {

        this.showError(error.status, error.json().message);
      })
  }

  private loadEtatPaiementList() {
    this.facturationService.getEtatPaiementList()
      .subscribe(etatPaiementList => {
          this.etatPaiementList = etatPaiementList;
          this.facturationForm.patchValue({etatPaiement: this.facturation.etatPaiement}, {onlySelf: true});
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadTypePaiementList() {
    this.facturationService.getTypePaiementList()
      .subscribe(typePaiementList => {
          this.typePaiementList = typePaiementList;
          this.facturationForm.patchValue({typePaiement: 'ESPECE'}, {onlySelf: true});
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadClientList() {
    this.clientService.getOnlyClientList()
      .subscribe(clientList => {
          this.clientList = clientList;
          this.facturation.client = this.clientList.find(client => client.id === this.facturation.client.id);
          this.facturationForm.patchValue({client: this.facturation.client.nom}, {onlySelf: true});
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadBanqueList() {
    this.banqueService.getBanqueList()
      .subscribe(banqueList => {
          this.banqueList = banqueList;
          // if (this.facturationForm.get('typePaiement').value != 'ESPECE') {
          //   this.facturation.banque = this.banqueList.find(banque => banque.id === this.facturation.banque.id);
          //   this.facturationForm.patchValue({banque: this.facturation.banque}, {onlySelf: true});
          // }
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  checkTypePaiement() {
    let typePaiement = this.facturationForm.get('typePaiement').value;
    if (typePaiement != '' && typePaiement != null) {
      if (typePaiement == 'ESPECE') {
        this.facturationForm.get("banque").setValue(null);
        this.facturationForm.get("numeroCheque").setValue('');
        return true;
      }
      else {
        return false;
      }
    } else {
      return true;
    }
  }

  checkBanqueNumCheque() {
    let typePaiement = this.facturationForm.get('typePaiement').value;
    if ((typePaiement == 'CHEQUE' && this.facturationForm.get('banque').value == null) ||
      (typePaiement == 'CHEQUE' && this.facturationForm.get('numeroCheque').value == '')) {
      return false;
    }
    else {
      return true;
    }
  }

  isPaid() {
    if (this.facturationForm.get('etatPaiement').value == 'PAID') {
      return true;
    } else {
      return false;
    }
  }

  private disableFacturation() {
    this.facturationForm.controls['date'].reset({
      value: this.facturation.date,
      disabled: true
    });
    this.facturationForm.controls['typePaiement'].reset({
      value: this.facturation.typePaiement,
      disabled: true
    });
    this.facturationForm.controls['montantReste'].reset({
      value: this.facturation.montantReste,
      disabled: true
    });
    this.facturationForm.controls['banque'].reset({
      value: this.facturation.banque,
      disabled: true
    });
    this.facturationForm.controls['numeroCheque'].reset({
      value: this.facturation.numeroCheque,
      disabled: true
    });

  }

  checkMontant() {
    let montantReste = this.facturationForm.get('montantReste').value;
    if (montantReste <= 0 || montantReste > this.facturation.montantReste) {
      this.facturationForm.patchValue({montantReste: this.facturation.montantReste}, {onlySelf: true});
    }
  }

  isMontantResteEqualZero() {
    if (this.facturationForm.get('montantReste').value == 0) {
      return true;
    } else {
      return false;
    }
  }

}






