import { Client } from "../../client/metier/client.metier";
import { Banque } from "../../admin/banque/metier/banque.metier";
import { Maintenance } from "../../maintenance/metier/maintenance.metier";
import { Installation } from "../../installation/metier/installation.metier";

export class Facturation {
  id: number;
  numero: number = 0;
  date: Date = null;
  etatPaiement: string = null;
  typePaiement: string = null;
  typeFacturationService: string = null;
  client: Client = null;
  maintenance: Maintenance;
  nouvelleInstallation: Installation;
  montant: number = 0;
  montantPaye: number = 0;
  montantReste: number = 0;
  banque: Banque = null;
  numeroCheque: string = '';

  constructor(values: Object = {}) {
    Object.assign(this, values);
  }
}



