"use strict";

import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {fadeInAnimation} from "../../core/route-animation/route.animation";
import {Facturation} from "../metier/facturation.metier";
import {BaseComponent} from "../../core/component/base-component";
import {FacturationService} from "../service/facturation.service";
import {FormBuilder, FormGroup} from "@angular/forms";
import {Client} from "../../client/metier/client.metier";
import {ClientService} from "../../client/service/client.service";
import {PageTitleService} from "../../core/page-title/page-title.service";
import {Maintenance} from "../../maintenance/metier/maintenance.metier";
import {MaintenanceService} from "../../maintenance/service/maintenance.service";
import {ReglementService} from "../../reglement/service/reglement.service";
import {Reglement} from "../../reglement/metier/reglement.metier";


@Component({
  selector: 'ms-facturation-list',
  templateUrl: './facturation.list.component.html',
  styleUrls: ['./facturation.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})

export class FacturationListComponent extends BaseComponent implements OnInit {

  public facturationForm: FormGroup;
  private facturation: Facturation;
  private facturationList: Array<Facturation> = [];
  private reglementList: Array<Reglement> = [];
  private typeFacturationServiceList: string[] = [];
  private clientList: Array<Client> = [];
  private maintenanceList: Array<Maintenance> = [];

  constructor(private fb: FormBuilder, private pageTitleService: PageTitleService,
              private facturationService: FacturationService,
              private clientService: ClientService,
              private maintenanceService: MaintenanceService,
              private reglementService: ReglementService) {
    super();
    this.createForm();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES FACTURATIONS");
    this.loadFacturationList();
    this.loadClientList();
    this.loadTypeFacturationServiceList();
  }

  createForm() {
    this.facturationForm = this.fb.group({
      numero: null,
      client: null,
      typeContrat: '',
      etatPaiement: '',
      typePaiement: '',
      typeFacturationService:'',
      maintenance: null,
    })
    ;
  }


  private loadFacturationList() {

    this.facturationService.getFacturationList()
      .subscribe(facturationList => {
        this.facturationList = facturationList;

      }, error => {

        this.showError(error.status, error.json().message);
      });
  }

  findFacturationList(facturationForm: any) {

    this.facturation = new Facturation(facturationForm);
    this.facturationService.findFacturationListByCriteria(this.facturation)
      .subscribe(facturationList => {
        this.facturationList = facturationList;

      }, error => {

        this.showError(error.status, error.json().message);
      });
  }


  private loadClientList() {
    this.clientService.getOnlyClientList()
      .subscribe(clientList => {
          this.clientList = clientList;
        }, error => this.showError(error.status, error.json().message)
      );
  }

  
  private loadTypeFacturationServiceList() {
    this.facturationService.getTypeFacturationServiceList()
      .subscribe(typeFacturationServiceList => {
          this.typeFacturationServiceList = typeFacturationServiceList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  reglementRedirect(facturation: Facturation) {

    this.reglementService.getReglementByFacturation(facturation)
      .subscribe(reglementList => {

        if (reglementList.length == 1) {
          //rederect to detail de reglement
          // console.log('rederect to detail de reglement => ' + reglementList[0].id);
          this.router.navigate(['/reglement/edit/', reglementList[0].id]);

        } else {
          //rederict to list of reglement de cette facturation
          // console.log('rederict to list of reglement de cette facturation');
          this.router.navigate(['/reglement/list'], {queryParams: {facturation_id: facturation.id}});
        }


      }, error => {

        this.showError(error.status, error.json().message);
      });

  }

}



