import {Routes} from '@angular/router';

import {FacturationListComponent} from "./facturation.list/facturation.list.component";
import {FacturationEditComponent} from "./facturation.edit/facturation.edit.component";

export const FacturationRoutes: Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [
    {path: 'list', component: FacturationListComponent},
    {path: 'edit/:id', component: FacturationEditComponent},
  ]
}];



